import { FindAllMenu } from '@/api/modules/system/common';
import { Button, Checkbox, Col, Form, Input, InputNumber, Modal, Radio, Row, Space, Switch, Tree } from 'antd';
import TextArea from 'antd/es/input/TextArea';
import { useEffect, useState } from 'react';

const ModalComponent = (Params: any) => {
	const { form, modalIsVisible, setModalIsVisible, modalTitle, modalType, modalUserInfo: userInfo, handleModalSubmit } = Params;

	const [menuList, setMenuList] = useState([]);
	const [expandedKeys, setExpandedKeys] = useState([]); // Â±ïÂºÄ
	const [checkedKeys, setCheckedKeys] = useState<any>([]); // ÂÖ®ÈÄâ

	function transformRoutes(routes: any[]) {
		return routes.map((route: any) => {
			const item: any = {
				title: route.meta?.title || '',
				key: route.meta?.key || '',
			};
			if (Array.isArray(route.children) && route.children.length > 0) {
				item.children = transformRoutes(route.children);
			}
			return item;
		});
	}

	useEffect(() => {
		FindAllMenu({}).then((res: any) => {
			setMenuList(transformRoutes(res.data || []) as any);
			setExpandedKeys([]);
			setCheckedKeys(modalType == 'create' ? '' : userInfo.permission_menu);
			form.setFieldsValue({
				role_name: modalType == 'create' ? '' : userInfo.role_name,
				permission_str: modalType == 'create' ? '' : userInfo.permission_str,
				level: modalType == 'create' ? 1 : userInfo.level,
				sort: modalType == 'create' ? 1 : userInfo.sort,
				status: modalType == 'create' ? false : userInfo.status,
				desc: modalType == 'create' ? '' : userInfo.desc,
			});
		});
	}, [modalType, userInfo]);

	// ÂèñÊ∂àÊåâÈíÆ
	const OnCancel = () => {
		setExpandedKeys([]);
		setCheckedKeys([]);
		setModalIsVisible(false);
	};

	// * Êèê‰∫§ÊúÄÁªàÊï∞ÊçÆ ÔºàÂ∞ÜËèúÂçïÂ§ÑÁêÜ‰∏∫menuÊ†ºÂºè„ÄÅ‰∏∫ÊØè‰∏™ËßíËâ≤ÂèØ‰ª•Áõ¥Êé•‰ΩøÁî®ÁöÑËèúÂçïÁªìÊûÑÔºâ
	const FormOnFinish = () => {
		// üîß Step 1ÔºöÈÄíÂΩíÊü•ÊâæÊüê‰∏™ key ÁöÑË∑ØÂæÑ
		function findPathByKey(tree: any, targetKey: any, path = []) {
			for (const node of tree) {
				const currentKey = node.key;
				const newPath: any = [...path, currentKey];
				if (currentKey === targetKey) {
					return newPath;
				}
				if (node.children && node.children.length > 0) {
					const found: any = findPathByKey(node.children, targetKey, newPath);
					if (found) return found;
				}
			}
			return null;
		}
		function findPathsForKeys(tree: any, keys: any[]) {
			const result = new Set(); // ‰ΩøÁî® Set ÈÅøÂÖçÈáçÂ§ç
			keys.forEach((key: any) => {
				const path = findPathByKey(tree, key);
				if (path) {
					path.forEach((k: unknown) => result.add(k)); // Áà∂ËäÇÁÇπ‰πüÂä†ÂÖ•
				}
			});
			return Array.from(result); // ÊúÄÁªàËøîÂõûÊâÅÂπ≥ key Êï∞ÁªÑ
		}
		const flatKeys = findPathsForKeys(menuList, checkedKeys);
		// console.log('ÊâÄÊúâËèúÂçïÔºö', menuList);
		// console.log('checkedKeys', checkedKeys);
		// console.log('flatKeys', flatKeys); // Ëé∑ÂèñÂà∞ÊâÄÊúâÁöÑÁà∂Â≠êËèúÂçïÔºö ['menu', 'menu2', 'menu22', 'menu221', 'menu222']
		const formList = form.getFieldsValue();
		if (modalType == 'edit') {
			formList._id = userInfo._id;
		}
		formList.permission_menu = checkedKeys; // Êó†Áà∂ËäÇÁÇπÔºö['pageMenu']
		formList.menuList = flatKeys; // ÊúâÁà∂ËäÇÁÇπÔºö['auth', 'pageMenu']
		handleModalSubmit && handleModalSubmit(modalType, formList);
	};

	const getAllKeys = (nodes: any) => {
		const keys: any[] = [];
		const traverse = (arr: any[]) => {
			arr.forEach((item: { key: any; children: any }) => {
				keys.push(item.key);
				if (item.children) traverse(item.children);
			});
		};
		traverse(nodes);
		return keys;
	};
	// Â±ïÂºÄ/ÊäòÂè†
	const ExpandedFunc = (e: any) => {
		const isTrue = e.target.checked;
		if (isTrue) {
			const allKeys: any = getAllKeys(menuList);
			setExpandedKeys(allKeys);
		} else {
			setExpandedKeys([]);
		}
	};
	// ÂÖ®ÈÄâ/ÂÖ®‰∏çÈÄâ
	const SelectAllFunc = (e: any) => {
		const isTrue = e.target.checked;
		if (isTrue) {
			const allKeys: any = getAllKeys(menuList);
			setCheckedKeys(allKeys);
		} else {
			setCheckedKeys([]);
		}
	};
	const OnSubmit = () => {
		form.submit();
	};
	return (
		<Modal
			className='relative'
			title={modalTitle}
			width={600}
			open={modalIsVisible}
			onCancel={OnCancel}
			footer={[
				<Button danger loading={false} onClick={OnCancel}>
					ÂèñÊ∂à
				</Button>,
				<Button key='link' type='primary' loading={false} onClick={OnSubmit}>
					Êèê‰∫§
				</Button>,
			]}
		>
			<Form className='mb-[60px] max-h-[600px] overflow-auto' layout='horizontal' form={form} labelCol={{ span: 4 }} wrapperCol={{ span: 18 }} onFinish={FormOnFinish}>
				<Row gutter={16}>
					<Col span={24}>
						<Form.Item label='ËßíËâ≤ÂêçÁß∞' name='role_name' rules={[{ required: true, message: 'ÂøÖÂ°´ÔºöËßíËâ≤ÂêçÁß∞' }]}>
							<Input placeholder='ËØ∑ËæìÂÖ•ËßíËâ≤ÂêçÁß∞' />
						</Form.Item>
					</Col>
					<Col span={24}>
						<Form.Item label='ÊùÉÈôêÂ≠óÁ¨¶' name='permission_str' rules={[{ required: true, message: 'ÂøÖÂ°´ÔºöÊùÉÈôêÂ≠óÁ¨¶' }]}>
							<Input placeholder='ËØ∑ËæìÂÖ•ÊùÉÈôêÂ≠óÁ¨¶' />
						</Form.Item>
					</Col>
					<Col span={24}>
						<Form.Item label='ËßíËâ≤Á∫ßÂà´' name='level' rules={[{ required: true, message: 'ÂøÖÂ°´ÔºöËßíËâ≤Á∫ßÂà´' }]}>
							<InputNumber defaultValue={1} className='always-show-handler' keyboard={false} />
						</Form.Item>
					</Col>
					<Col span={24}>
						<Form.Item label='ËßíËâ≤È°∫Â∫è' name='sort' rules={[{ required: true, message: 'ÂøÖÂ°´ÔºöËßíËâ≤È°∫Â∫è' }]}>
							<InputNumber defaultValue={1} className='always-show-handler' keyboard={false} />
						</Form.Item>
					</Col>
					<Col span={24}>
						<Form.Item label='Áä∂ÊÄÅ' name='status' rules={[{ required: false }]}>
							<Switch />
						</Form.Item>
					</Col>
					<Col span={24}>
						<Form.Item label='ËèúÂçïÂàÜÈÖç' name='permission_menu' rules={[{ required: false, message: 'ÂøÖÂ°´ÔºöËèúÂçïÊùÉÈôê' }]}>
							<div className='mt-[6px] px-3 w-full flex justify-between'>
								<div>
									<Checkbox onChange={ExpandedFunc}>Â±ïÂºÄ/ÊäòÂè†</Checkbox>
								</div>
								<div>
									<Checkbox onChange={SelectAllFunc}>ÂÖ®ÈÄâ/ÂÖ®‰∏çÈÄâ</Checkbox>
								</div>
								<div>
									<Checkbox defaultChecked onChange={() => {}}>
										Áà∂Â≠êËÅîÂä®
									</Checkbox>
								</div>
							</div>
							<div className='mt-3 w-full p-3  border-[1px] rounded-lg'>
								<Tree
									// checkStrictly={!linkage} // Áà∂Â≠êËÅîÂä®
									checkStrictly={false}
									showLine
									checkable
									treeData={menuList}
									checkedKeys={checkedKeys}
									expandedKeys={expandedKeys}
									// Ëá™Âä®ÂØªÊâæÁà∂Á∫ßÂêóÔºü
									onExpand={(keys: any) => {
										setExpandedKeys(keys);
									}}
									onCheck={(keys: any) => {
										setCheckedKeys(keys);
									}}
								/>
							</div>
						</Form.Item>
					</Col>
					<Col span={24}>
						<Form.Item label='ËßíËâ≤ÊèèËø∞' name='desc' rules={[{ required: false }]}>
							<TextArea rows={3} placeholder='ËØ∑ËæìÂÖ•ÂÜÖÂÆπ' maxLength={60} />
						</Form.Item>
					</Col>
				</Row>
			</Form>
		</Modal>
	);
};
export default ModalComponent;
